:root {
    --br: 12px;
    --bg: #fafafa;
    --db: #0065ff;
    --db-dark: #034dbe;
    --grey: #c9c9c9;
    --placeholder: #b0b0b0;
    --shadow: #808080;
	--dg: #259c39;
}

html, body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
	font-family: 'Poppins', sans-serif;
    background-color: #fafafa;
    background-color: var(--bg);
}

body {
    padding-bottom: 20px;
}

p, h1, h2, h3, h4, h5, h6, ul, li {
    margin: 0;
    padding: 0;
}

input, textarea, button {
    font-family: 'Poppins', sans-serif;
}

.store-main-left-top {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	grid-gap: 15px;
	gap: 15px;
}

.store-main-left-top img {
	width: 20px;
}

input:focus, button:focus, a:focus, select:focus {
	outline-color: #034dbe;
	outline-color: var(--db-dark);
}

.dg {
